.heading {
  font-size: 1.5rem;
  font-weight: bold;
  justify-self: center;
  color: #1b4aa2; }
  .heading span {
    color: #333333; }

html {
  box-sizing: border-box;
  font-size: 62.5%; }
  @media (max-width: 112.5em) {
    html {
      font-size: 75%; } }
  @media (max-width: 75em) {
    html {
      font-size: 56.25%; } }
  @media (max-width: 56.25em) {
    html {
      font-size: 50%; } }
  @media (max-width: 37.5em) {
    html {
      font-size: 35%; } }

body {
  font-family: -apple-system, BlinkMacSystemFont, "Roboto", "Droid Sans", "Helvetica Neue", Arial, sans-serif;
  color: #000000;
  font-weight: 300;
  line-height: 1.6;
  margin: 0px;
  padding: 0px; }

.container {
  display: grid;
  grid-template-rows: repeat(2, minmax(min-content, 6vh)) 1fr;
  grid-template-columns: 2% 90% 8%; }

input {
  width: 100%; }

input {
  box-sizing: border-box;
  border: 1px solid #333333; }

.btn {
  background-color: #1b4aa2;
  color: #ffffff;
  border: none;
  font-size: 1rem;
  padding: 0.65rem 0;
  cursor: pointer;
  width: 100%; }
  .btn__contactclear {
    margin-top: 1em;
    background-color: #333333; }
  .btn__contactedit {
    background-color: #333333;
    width: auto;
    padding: 0.5em 2em; }
  .btn__contactdelete {
    background-color: #e04958;
    width: auto;
    padding: 0.5em 2em; }

.alert {
  grid-column: 1 / -1;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr; }
  .alert .show {
    background-color: #e04958;
    color: #ffffff; }
  .alert h3 {
    grid-column: 2 / 3;
    background-color: #e04958;
    padding: 0.25em;
    color: #ffffff;
    cursor: pointer; }

.header {
  background-color: #1b4aa2;
  color: #ffffff;
  grid-column: 1 / -1;
  display: grid;
  grid-template-columns: 1fr min-content;
  justify-content: end; }
  .header__menu {
    display: grid;
    align-items: center;
    padding: 0px 1rem;
    justify-self: end; }
    .header__menu a:link, .header__menu a:visited {
      text-decoration: none;
      color: #ffffff;
      padding: 1rem 0px;
      font-weight: bold; }
    .header__menu a.selected {
      color: yellow; }
    .header__menu:hover {
      background-color: yellow;
      cursor: pointer; }
      .header__menu:hover a, .header__menu:hover div {
        color: #000000; }

.login {
  grid-column: 2 / 3;
  display: grid;
  grid-template-rows: repeat(6, min-content);
  justify-content: center;
  grid-template-columns: minmax(25%, 250px); }
  .login__field {
    margin: 0.75rem 0px; }
    .login__field--label {
      margin-bottom: 1em; }
    .login__field--input input, .login__field--input input[type="button"] {
      width: 100%;
      font-size: 1rem;
      padding: 0.5rem; }
    .login__field--input input[type="radio"] {
      margin: 0; }

.register {
  grid-column: 2 / 3;
  display: grid;
  grid-template-rows: repeat(6, min-content);
  justify-content: center;
  grid-template-columns: minmax(25%, 250px); }
  .register__field {
    margin: 0.75rem 0px; }
    .register__field--label {
      margin-bottom: 1em; }
    .register__field--input input, .register__field--input input[type="button"] {
      width: 100%;
      font-size: 1rem;
      padding: 0.5rem; }
    .register__field--input input[type="radio"] {
      margin: 0; }

.dashboard {
  grid-column: 2 / 3;
  display: grid;
  grid-gap: 1em;
  grid-template-columns: repeat(2, 1fr); }

.contactadd {
  grid-column: 1 / 2;
  display: grid;
  grid-template-rows: repeat(6, min-content);
  justify-content: center;
  grid-template-columns: minmax(25%, 80%); }
  .contactadd__field {
    margin: 0.75rem 0px; }
    .contactadd__field--label {
      margin-bottom: 1em; }
    .contactadd__field--input input, .contactadd__field--input input[type="button"] {
      width: 100%;
      font-size: 1rem;
      padding: 0.5rem; }
    .contactadd__field--input input[type="radio"] {
      margin: 0; }
  .contactadd .contactadd__field--input-contacttype {
    display: grid;
    grid-template-columns: repeat(4, min-content);
    align-items: center;
    grid-gap: 0.5em; }

.contactedit {
  grid-column: 1 / 2;
  display: grid;
  grid-template-rows: repeat(6, min-content);
  justify-content: center;
  grid-template-columns: minmax(25%, 80%); }
  .contactedit__field {
    margin: 0.75rem 0px; }
    .contactedit__field--label {
      margin-bottom: 1em; }
    .contactedit__field--input input, .contactedit__field--input input[type="button"] {
      width: 100%;
      font-size: 1rem;
      padding: 0.5rem; }
    .contactedit__field--input input[type="radio"] {
      margin: 0; }
  .contactedit .contactedit__field--input-contacttype {
    display: grid;
    grid-template-columns: repeat(4, min-content);
    align-items: center;
    grid-gap: 0.5em; }

.contactfilter {
  grid-column: 2 / 3; }
  .contactfilter input {
    width: 100%;
    font-size: 1rem;
    padding: 0.5rem; }
  .contactfilter__nocontacts {
    margin-top: 1em;
    font-size: 1.25em;
    font-weight: bold;
    display: none; }

.contactdetails {
  background-color: #f4f4f4;
  border: 1px solid #e7e7e7;
  padding: 1em;
  margin: 1em 0;
  display: grid;
  grid-template-rows: repeat(4, min-content);
  align-items: center;
  grid-gap: 0.5em; }
  .contactdetails .fa {
    padding-right: 0.25em; }
  .contactdetails__heading {
    display: grid;
    grid-template-columns: max-content min-content;
    grid-gap: 1em;
    align-items: center; }
    .contactdetails__heading--name {
      font-size: 1.5em;
      color: #1b4aa2; }
    .contactdetails__heading--type {
      background-color: #1b4aa2;
      color: #ffffff;
      padding: 0.25em 1.5em;
      font-size: 1.1em;
      -webkit-border-radius: 3px;
      -moz-border-radius: 3px;
      -ms-border-radius: 3px;
      -o-border-radius: 3px;
      border-radius: 3px; }
  .contactdetails__btn {
    grid-column: 1 / -1;
    display: grid;
    grid-template-columns: repeat(2, min-content);
    grid-gap: 0.5em; }

.hide {
  display: none; }

.show {
  display: inline-block; }

.hidden {
  visibility: hidden; }
